import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"

import styles from './style.module.scss';

interface State {
  action: string
  commandString: string
  commandOptions: string
  isFocused: boolean
}

declare global {
  interface Window {
    chrome: any;
  }
}

class RunCommand extends StreamlitComponentBase<State> {
  public state = { 
    action: "RunCommand",
    commandString: "", 
    commandOptions: "",
    isFocused: false }

  public render = (): ReactNode => {
    
    const imgStyle = {
      margin: "0px 3px 2px 0px"
    }
    
    return (
      <span>
        <button
          className={styles.poButton}
          onClick={this.onClicked}
          disabled={this.props.disabled}
          onFocus={this._onFocus}
          onBlur={this._onBlur}
        >
          <span>
            <img style={imgStyle} src={process.env.PUBLIC_URL + '/img/pollination.png'} alt="logo" />
          </span>
            Run Command
        </button>
      </span>
    )
  }

  private onClicked = (): void => {
    if (typeof window.parent.chrome != "undefined") {
      if (typeof window.parent.chrome.webview == "undefined")
      {
        console.log('[POLLINATION-DEBUG]: webview not found.');
        return;
      }
      
      var msgObject = {
        action: "RunCommand",
        commandString: this.props.args["commandString"],
        commandOptions: this.props.args["commandOptions"]
      };
      var json = JSON.stringify(msgObject);
      window.parent.chrome.webview.postMessage(json);

      this.setState(
        (prevState) => ({ 
          ...prevState, 
          action: msgObject.action,
          commandString: msgObject.commandString,
          commandOptions: msgObject.commandOptions
        }),
        () => Streamlit.setComponentValue(this.state.action)
      );
      console.log('[POLLINATION-DEBUG]: JSON string sent.');
    }
    else
    {
      console.log('[POLLINATION-DEBUG]: chrome not found.');
    }
  }

  private _onFocus = (): void => {
    this.setState({ isFocused: true })
  }

  private _onBlur = (): void => {
    this.setState({ isFocused: false })
  }
}

export default withStreamlitConnection(RunCommand)
